  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
}
.fancy-layout .field-richeditor {
  border: none;
  border-left: 1px solid #d1d6d9 !important;
}
.fancy-layout .field-richeditor,
.fancy-layout .field-richeditor .fr-toolbar,
.fancy-layout .field-richeditor .fr-wrapper {
  -webkit-border-radius: 0;
  -moz-border-radius: 0;
  border-radius: 0;
  border-top-right-radius: 0;
  border-top-left-radius: 0;
}
.fancy-layout .secondary-content-tabs .field-richeditor .fr-toolbar {
  background: white;
}
body.side-panel-not-fixed .fancy-layout .field-richeditor {
  border-left: none;
}
html.cssanimations .fancy-layout .form-tabless-fields .loading-indicator-container .loading-indicator > span {
  -webkit-animation: spin 1s linear infinite;
  animation: spin 1s linear infinite;
  background-image: url('../../../system/assets/ui/images/loader-white.svg');
  background-size: 20px 20px;
}
html.gecko .fancy-layout .control-tabs.secondary-tabs > div > ul.nav-tabs > li.active a {
  padding-top: 13px;
}
.flyout-container > .flyout {
  overflow: hidden;
  width: 0;
  left: 0 !important;
  -webkit-transition: width 0.1s;
  transition: width 0.1s;
}
.flyout-overlay {
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 5000;
  position: absolute;
  background-color: rgba(0, 0, 0, 0);
  -webkit-transition: background-color 0.3s;
  transition: background-color 0.3s;
}
.flyout-toggle {
  position: absolute;
  top: 20px;
  left: 0;
  width: 23px;
  height: 25px;
  background: #2b3e50;
  cursor: pointer;
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
  color: #bdc3c7;
  font-size: 10px;
}
.flyout-toggle i {
  margin: 7px 0 0 6px;
  display: inline-block;
}
.flyout-toggle:hover i {
  color: #ffffff;
}
body.flyout-visible {
  overflow: hidden;
}
body.flyout-visible .flyout-overlay {
  background-color: rgba(0, 0, 0, 0.3);
}
/**********************************
 * Skin edits
 *
 */
/**********************************
