}
.fancy-layout .form-tabless-fields {
  padding: 6px 23px 0 23px;
}
.fancy-layout .control-tabs.primary-tabs > div > ul.nav-tabs > li a {
  padding: 0 16px;
}
/**********************************
* Static Pages plugin sidebar
*
*/
#layout-side-panel .fix-button {
  display: none !important;
}
/**********************************
 * Field sizes
 *
 */
.layout .field-richeditor.size-giant .redactor-editor {
  height: 760px !important;
}
